* {
  margin: 0;
  padding: 0;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.header {
  display: flex;
  align-items: center;
  background-color: #000;
  width: 100%;
  color: #fff;
  height: 50px;
  padding: 0 20px;
  box-sizing: border-box;
  justify-content: center;
}
.header h4 {
  font-weight: 900;
  
}
.list_item {
  padding: 20px;
  text-align: left;
  border-bottom: 1px solid #ccc;
  
}



.list_item_top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.list_item_cont {
  margin-bottom: 10px;
}
.list_item_date {
  
  font-size: 12px;
  color: #666;
}
.btn_like {
  border: none;
  padding: 0;
  margin: 0;
  background-color: #fff;
  cursor: pointer;
  font-size: 16px;
}

.modal {
 
  background-color: #e4e4e4;
  padding: 30px 20px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  position: fixed;
  left: 20px;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  box-shadow: 0 0 30px rgba(0,0,0, .5);
}

.inputBox {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px 20px 0;
  gap: 10px;
}
.inputBox input {
  width: 100%;
  height: 30px;
}
.inputBox textarea {
  flex: 1;
  height: 80px;
} 
.inputBox button {
  width: 50px;
  flex-shrink: 0;
}
